import React,{useState,useEffect,useRef} from 'react'
import ReactDOM from 'react-dom'
import NumberFormat from 'react-number-format'
import { inject, observer } from 'mobx-react';
import styled from 'styled-components';
const Wrapper = styled(NumberFormat)`
  width : ${props => props.width}px;
`


function DeriNumberFormat(props){
  const [renderablity, setRenderablity] = useState(<span className='loading-line'></span>);
  const ref = useRef()
  const isValidate = () => {
    const {allowZero} = props 
    return (typeof props.value !== 'object' && props.value !== undefined && props.value !== '' && !isNaN(props.value) && props.value !== 'NaN') 
          || ((isNaN(props.value) && Math.abs(props.value)) === 0 && allowZero === true )
  }

  useEffect(() => {
    const {allowZero,wallet,defaultValue = '--',...others} = props 
    if(isValidate()) {
      others.value = String(others.value)
      setRenderablity(<Wrapper {...others}  displayType = 'text'  fixedDecimalScale={true} thousandSeparator={true}/>)
    } else {
      setRenderablity(<span className='loading-line'></span>)
    } 
    const timeout = window.setTimeout(() => {
      // && wallet && (!wallet.isConnected() || !wallet.supportChain)
      if(!isValidate()){
        setRenderablity(defaultValue)
      }
    },30000)
    return () => {
      clearTimeout(timeout)
    };
  }, [props.value]);

  return renderablity;
}
export default DeriNumberFormat
